
.mod-index {
  padding-top: 1.333333rem;
}
.ui-header {
  width: 100%;
  height: 1.44rem;
  font-size: 0.426667rem;
  position: fixed;
  top: 0;
  z-index: 20;
}
.ui-header .list-icon {
  margin-left: 0.266667rem;
}
.ui-header .sy-txt {
  margin-left: 0.8rem;
}
.ui-header .head-more {
  margin-left: 0.533333rem;
}
.ui-header .edit-icon {
  font-size: 0.533333rem;
  margin-right: 0.16rem;
}
.mint-swipe {
  height: 6.133333rem;
}
.mint-swipe .ui-swiper {
  width: 100%;
  height: 100%;
  position: relative;
  background-size: 100%;
  background-position: 50%;
  background-repeat: no-repeat;
}
.mint-swipe .ui-swiper .slider-title {
  position: absolute;
  bottom: 0.613333rem;
  line-height: 1.2;
  left: 0;
  padding: 0 0.48rem;
  font-weight: 300;
  font-size: 0.56rem;
  color: #ffffff;
  z-index: 12;
}
.mint-swipe .swiper-mask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: -webkit-gradient(linear, left top, left bottom, from(bottom), color-stop(rgba(0, 0, 0, 0.7)), color-stop(40%, rgba(0, 0, 0, 0.1)), to(rgba(0, 0, 0, 0.1)));
  background-image: linear-gradient(bottom, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.1) 40%, rgba(0, 0, 0, 0.1));
  background-image: -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.1) 40%, rgba(0, 0, 0, 0.1));
}
.mint-swipe .mint-swipe-indicator {
  width: 0.213333rem;
  height: 0.213333rem;
}
.sidebar-mask {
  /*width: 357px;*/
  /*height: 667px;*/
  position: fixed;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 30;
  background: rgba(0, 0, 0, 0.7);
}
#jApp.night-style {
  background: #343434;
}
#jApp.night-style .ui-header {
  background: #222222;
}
#jApp.night-style .mod-list {
  background: #343434;
}
#jApp.night-style .mod-list .title {
  color: #b7b7b7;
}
#jApp.night-style .mod-list .list-item {
  background: #404040;
  border: 0.026667rem solid #343434;
  border-bottom: 0.026667rem solid #222222;
}
#jApp.night-style .mod-list .list-item .item-title {
  color: #d2d2d2;
}
.scroll-stop {
  overflow: hidden;
}


